import Swiper, { Pagination } from 'swiper';

export default () => {
  let btn = '.mainServices__right-button',
    serv = '.mainServices__service',
    bg = '.mainServices__bg';

  $(window).on('load', function () {
    let active = $('.mainServices__right-button.active');
    if (active != null) {
      active.prop('disabled', true);
    }

    $('.mainServices__right-button').on('click', function () {
      $(btn).prop('disabled', false);
      $(btn).removeClass('active');
      $(serv).removeClass('active');
      $(bg).removeClass('active');

      let data = this.dataset.service;
      let ds = `[data-service=${data}]`;

      $(this).prop('disabled', true);
      $(this).addClass('active');
      $(bg + ds).addClass('active');
      $(serv + ds).addClass('active');
    });
  });

  let windowWidth = window.innerWidth;
  let servSwiper;
  let inits = false;

  $(window).on('resize load', function () {
    windowWidth = window.innerWidth;
    if (windowWidth < 768) {
      if (!inits) {
        let array = ['proj', 'prod', 'sup', 'assem', 'equip'];
        inits = true;
        Swiper.use([Pagination]);
        servSwiper = new Swiper('#mainServSlider', {
          slidesPerView: 1,
          speed: 400,
          spaceBetween: 30,
          autoHeight: true,
          pagination: {
            el: '#mainServPage',
            type: 'bullets',
            clickable: true,
            renderBullet: function (index, className) {
              return `<span class="${className}" data-service=${array[index]}></span>`;
            },
          },
          on: {
            init() {
              setTimeout(function () {
                $('.mainServices__right-button').each(function (i) {
                  if ($(this).hasClass('active')) {
                    servSwiper.slideTo(i);
                    servSwiper.update();
                  }
                });
              }, 10);
            },
          },
        });
        $('#mainServPage .swiper-pagination-bullet').on('click', function () {
          $(btn).prop('disabled', false);
          $(btn).removeClass('active');
          $(serv).removeClass('active');
          $(bg).removeClass('active');

          let data = this.dataset.service;
          let ds = `[data-service=${data}]`;

          $(btn + ds).prop('disabled', true);
          $(btn + ds).addClass('active');
          $(bg + ds).addClass('active');
          $(serv + ds).addClass('active');
        });
      }
    } else {
      if (inits) {
        inits = false;
        servSwiper.destroy();
      }
    }
  });
};
