import Swiper, { Navigation } from 'swiper';
import updateFraction from '@blocks/slider/sliderNumbers';
export default () => {
  Swiper.use([Navigation]);
  let swiper = new Swiper('#trustSlider', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 15,
    navigation: {
      prevEl: '#trustSliderPrev',
      nextEl: '#trustSliderNext',
    },
    breakpoints: {
      768: {
        slidesPerView: 2,
      },
      1024: {
        slidesPerView: 3,
      },
    },
    on: {
      init() {
        setTimeout(updateFraction, 100, this, '.trust');
      },
      slideChange() {
        updateFraction(this, '.trust');
      },
      resize() {
        updateFraction(this, '.trust');
      },
    },
  });
};
