export default () => {
  $(window).on('resize load', function () {
    let windowWidth = window.innerWidth;
    if (windowWidth < 768) {
      return false;
    }

    let threeLine = $('#tankBenefitsThree');
    let autoLine = $('#tankBenefitsAuto');
    let leftLine = $('#tankBenefitsLeft');
    let qualLine = $('#tankBenefitsQual');
    let hitechLine = $('#tankBenefitsHitech');
    let fastLine = $('#tankBenefitsFast');
    let easyLine = $('#tankBenefitsEasy');

    if (windowWidth >= 1024) {
      dashed(threeLine, '-');
      dashed(leftLine, '+');
    } else {
      dashed(leftLine, '-');
    }

    dashed(qualLine, '+');
    dashed(hitechLine, '-');
    dashed(fastLine, '-');
    dashed(easyLine, '+');

    dashed(autoLine, '+');

    function dashed(elem, sign) {
      let box = elem.children('.tankBenefit__item-line');
      let x = box.width();
      let y = box.height();
      let line = box.children('span');
      line.width(Math.sqrt(x * x + y * y) + 'px');
      let atan2 = 57.33 * Math.atan2(y, x);
      line.css('transform', 'rotate(' + sign + atan2 + 'deg)');
    }
  });
};
