import Swiper, { Pagination, Navigation } from 'swiper';
export default () => {
  Swiper.use([Pagination, Navigation]);
  let swiper = new Swiper('.slider .swiper-container', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 10,
    centeredSlides: true,
    navigation: {
      prevEl: '.slider .swiper-button-prev',
      nextEl: '.slider .swiper-button-next',
    },
    pagination: {
      el: '.slider .swiper-pagination',
      type: 'bullets',
      clickable: true,
    },
    breakpoints: {
      768: {
        slidesPerView: 'auto',
        spaceBetween: 20,
        centeredSlides: false,
      },
      1024: {
        slidesPerView: 'auto',
        spaceBetween: 40,
        centeredSlides: false,
      },
    },
  });
  let resizeSwiper = false;
  $(window).on('resize', function () {
    let windowWidth = window.innerWidth;
    if (windowWidth >= 768) {
      if (!resizeSwiper) {
        resizeSwiper = true;
        $('.slider .swiper-slide').attr('style', null);
      }
    } else {
      resizeSwiper = false;
    }
  });
};
