import Swiper, { Pagination, Navigation } from 'swiper';
import updateFraction from '@blocks/slider/sliderNumbers';
export default () => {
  Swiper.use([Pagination, Navigation]);
  let swiper = new Swiper('#headingSlider', {
    slidesPerView: 1,
    speed: 1000,
    centeredSlides: true,
    spaceBetween: 0,
    navigation: {
      prevEl: '#headingSliderPrev',
      nextEl: '#headingSliderNext',
    },
    pagination: {
      el: '#headingPagination',
      type: 'bullets',
      clickable: true,
    },
    on: {
      init() {
        setTimeout(updateFraction, 100, this, '.heading');
      },
      slideChange() {
        updateFraction(this, '.heading');
      },
    },
  });
};
