$('.nav__item-link').each(function () {
  if (this.href == location.href) {
    this.disabled = true;
    $(this).attr('tabindex', -1);
    $(this).addClass('active');
  }
});

$('.navUtility__item-link').each(function () {
  if (this.href == location.href) {
    this.disabled = true;
    $(this).attr('tabindex', -1);
    $(this).addClass('active');
  }
});

