export default (slider, query, zero = true) => {
  let slidesNumber = slider.activeIndex + slider.params.slidesPerView,
    maxNumber =
      slider.slides
        .length; /* < 10
        ? '0' + slider.slides.length
        : slider.slides.length; */
  $(query + ' .slider__quantity-1st').text(slidesNumber);
  $(query + ' .slider__quantity-2nd').text(maxNumber);
};
